import React from "react";

function Entry(props) {
  return (
    <div className="term">
      <dt>
        <span className="emoji" role="img">
          {props.emoji}
        </span>
        <span>{props.name}</span>
      </dt><br />
      <dd>{props.meaning}</dd>
    </div>
  );
}

export default Entry;
